<template>
  <v-container grid-list-xs>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-card 
      class="mx-auto"
      max-width="900"
      outlined
      >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        KITSO Exam
      </v-card-title>
      <v-card-text class="my-3">
        <v-row>
          <v-col cols="12">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Schedule Name:</span>
              </v-col>
              <v-col cols="8">
                {{ exam.name }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Course:</span>
              </v-col>
              <v-col cols="8">
                {{ exam.course.name }}
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Start Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.startdate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">End Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.enddate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Status:</span>
              </v-col>
              <v-col cols="8">{{ exam.status.display }}</v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>

    <v-card 
      class="mx-auto"
      max-width="900"
      outlined
      >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        Exam Results
      </v-card-title>
      <v-card-text class="my-3">
        <v-row>
          <v-col cols="4">
            <v-btn color="secondary" dark small>
              <v-icon light left>mdi-playlist-check</v-icon>
              <span>Download Certificate</span>
            </v-btn>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="8">
            <v-simple-table>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">
                      Assessment
                    </th>
                    <th class="text-left">
                      Score
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="result in examResults"
                    :key="result.type"
                  >
                    <td>{{ result.type }}</td>
                    <td>
                      <v-chip
                        class="ma-2"
                      >
                        {{ result.score }}
                      </v-chip>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
            <v-divider/>
            <br>
            <center>
              <v-row class="justify-space-between">
                <v-col cols="4">
                  <span class="font-weight-bold">Overall Assessment:</span>
                </v-col>
                <v-col cols="8">
                  <v-chip @click.stop dark color="green" v-if="assessment === 'Pass'">
                    {{ assessment }}
                  </v-chip>
                  <v-chip @click.stop dark color="red" v-else-if="assessment === 'Fail' || assessment === 'Incomplete'">
                    {{ assessment }}
                  </v-chip>
                  <v-chip
                    class="ma-2"
                    v-else
                  >
                    {{ assessment }}
                  </v-chip>
                </v-col>
              </v-row>
          </center>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      exam: {
        enddate: "",
        status: {},
        course: {name: ""},
        assessments: []
      },
      examResults: [],
      assessment: "-"
    }
  },
  methods: {
    getExam() {
      fetch("/fhir/Basic/" + this.queries.schedule).then((response) => {
        response.json().then(async(exam) => {
          let assessments = exam.extension.filter((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/kitso-exam-assessment-type'
          })
          this.exam.assessments = []
          for(let assessment of assessments) {
            this.exam.assessments.push({
              code: assessment.valueCoding.code,
              display: assessment.valueCoding.display
            })
          }
          this.exam.name = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-basic-name'
          }).valueString
          this.exam.startdate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/start-date'
          }).valueDate
          this.exam.enddate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/end-date'
          }).valueDate
          this.exam.mode = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-mode-of-assessment'
          })?.valueCoding?.code
          let course = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-training-course'
          })?.valueReference?.reference
          await fetch("/fhir/Basic/" + course.split("/")[1]).then((response) => {
            response.json().then((response) => {
              this.exam.course.name = response.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-basic-name'
              }).valueString
              this.exam.course.passmark = response.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/training-course-pass-mark'
              }).valueDecimal
            })
          })
          let status = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-course-evaluated'
          })
          if(status) {
            if(status.valueCoding.code === 'yes') {
              status.valueCoding.display = "Evaluated and Approved"
            } else {
              status.valueCoding.display = "Evaluation in Progress"
              status.valueCoding.code === 'no'
            }
            this.exam.status = status.valueCoding
          }
          this.getResults()
        })
      })
    },
    getResults() {
      fetch("/fhir/Basic?kitsoscheduledexam=Basic/" + this.queries.schedule + "&practitioner=Practitioner/" + this.queries.practitioner + "&_profile=http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile").then(response => {
        response.json().then((data) => {
          if(this.exam.mode === 'score') {
            for(let assessment of this.exam.assessments) {
              let score = ""
              for(let ext of data.entry[0].resource.extension) {
                if(ext.url === "http://ihris.org/fhir/StructureDefinition/" + assessment.code + "-score") {
                  score = ext.valueDecimal
                  break
                }
              }
              this.examResults.push({
                type: assessment.display,
                score
              })
            }
          } else if(this.exam.mode === 'noscore') {
            let score = data.entry[0].resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/passfail-score'
            })?.valueCoding?.display
            if(!score) {
              score = ""
            }
            this.examResults.push({
              type: "Exam Assessment",
              score
            })
          }
          let assessment = data.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/exam-assessment'
          })
          if(assessment) {
            this.assessment = assessment.valueCoding.display
          }
        })
      })
    },
    getPractExam() {
      fetch("/fhir/Basic/" + this.queries.personexam).then((response) => {
        response.json().then(async(exam) => {
          this.queries.schedule = exam.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/kitso-exam-scheduling-reference"
          }).valueReference.reference
          this.queries.schedule = this.queries.schedule.split("/")[1]
          this.getExam()
        })
      })
    }
  },
  created() {
    if(this.queries.schedule) {
      this.getExam()
    } else {
      this.getPractExam()
    }
  }
}
</script>